<template>
  <div>
    <div class="form-wrapper">
      <p class="mb-2">
        USDOT number: {{ carrierInfo.usdot }}
      </p>

      <v-divider class="mb-6" />

      <v-form :disabled="isFormReadOnly">
        <base-field-input
          v-model="form.values.name"
          label="Carrier name "
          :is-required="true"
          :rules="form.carrierNameRules"
          :error="Boolean(serverError)"
          :error-messages="serverError"
        />

        <base-field-input
          v-model="form.values.address"
          label="Address "
          :is-required="true"
          :rules="form.addressRules"
          :error="Boolean(serverError)"
          :error-messages="serverError"
        />

        <base-field-select
          v-model="form.values.state"
          :items="states"
          label="State"
          :is-required="true"
          item-value="id"
          item-text="name"
          :rules="form.stateRules"
          :error="Boolean(serverError)"
          :error-messages="serverError"
        />

        <base-field-input
          v-model="form.values.city"
          label="City "
          :is-required="true"
          :rules="form.cityRules"
          :error="Boolean(serverError)"
          :error-messages="serverError"
        />

        <base-field-input
          v-model="form.values.zip"
          label="Zip"
        />

        <base-field-input
          v-model="form.values.contact_name"
          label="Contact name "
          :is-required="true"
          :rules="form.contactNameRules"
          :error="Boolean(serverError)"
          :error-messages="serverError"
        />

        <base-field-input
          mask="+1 (###) ###-####"
          v-model="form.values.phone"
          label="Phone number "
          :is-required="true"
          :rules="form.phoneRules"
          :error="Boolean(serverError)"
          :error-messages="serverError"
        />

        <base-field-input
          mask="+1 (###) ###-####"
          v-model="form.values.fax"
          label="Fax"
        />

        <base-field-input
          v-model="form.values.email"
          label="E-mail "
          :is-required="true"
          :rules="form.emailRules"
          :error="Boolean(serverError)"
          :error-messages="serverError"
        />

        <h3 class="my-2">
          Contact information for Billing / Accounting
        </h3>

        <v-divider class="mb-4" />

        <base-field-input
          v-model="form.values.contact_name"
          label="Contact name"
        />

        <base-field-input
          v-model="form.values.phoneNumbers"
          label="Phone numbers"
        />

        <base-field-input
          v-model="form.values.email"
          label="E-mail"
        />

        <base-field-textarea
          v-model="form.values.terms_and_conditions"
          label="Terms and Conditions"
        />

        <div class="d-flex flex-wrap justify-space-between">
          <v-btn
            :to="{ name: $constants.ROUTE_NAMES.CHANGE_PASSWORD }"
            :width="$vuetify.breakpoint.mobile ? '100%' : 'auto'"
            :height="$vuetify.breakpoint.mobile ? 50 : 35"
            v-if="!isFormReadOnly"
            depressed
            color="btn-background-grey"
            class="change-password-link primary--text mt-2 ma-lg-0 custom__btn"
          >
            Change Password
          </v-btn>
          <v-btn
            v-if="!isFormReadOnly"
            :width="$vuetify.breakpoint.mobile ? '100%' : 100"
            :height="$vuetify.breakpoint.mobile ? 50 : 35"
            depressed
            color="primary"
            class="mt-2 custom__btn"
            @click="onSubmitProfileInfo"
          >
            Save
          </v-btn>
        </div>
      </v-form>
    </div>
  </div>
</template>

<script>
import BaseFieldInput from '@components/BaseFieldInput';
import BaseFieldTextarea from '@components/BaseFieldTextarea';
import BaseFieldSelect from '@components/BaseFieldSelect';
import { required } from '@utils/helpers/validationRules';
import { createNamespacedHelpers } from 'vuex';
import * as constants from '@modules/profile/constants';

const { mapActions, mapState } = createNamespacedHelpers('profile');

export default {
  name: 'ProfileCarrierInfo',
  components: {
    BaseFieldInput,
    BaseFieldSelect,
    BaseFieldTextarea,
  },
  props: {
    isFormReadOnly: {
      type: Boolean,
      required: true,
    },
    states: {
      type: Array,
      required: true,
    },
  },
  watch: {
    'form.values': {
      deep: true,
      handler() {
        this.serverError = '';
        Object.keys(this.serverErrors).forEach(
          (key) => {
            this.serverErrors[key] = '';
          },
        );
      },
    },
  },
  data: () => ({
    form: {
      values: {
        name: '',
        address: '',
        state: '',
        city: '',
        zip: '',
        contact_name: '',
        phone: '',
        fax: '',
        email: '',
        phoneNumbers: '+1',
        terms_and_conditions: '',
      },
      carrierNameRules: [required('Carrier Name')],
      addressRules: [required('Address')],
      emailRules: [required('Email')],
      phoneRules: [required('Phone')],
      stateRules: [required('State')],
      cityRules: [required('City')],
      contactNameRules: [required('Contact Name')],
    },
    serverErrors: {},
    serverError: '',
  }),
  computed: {
    ...mapState({
      carrierInfo: (state) => state.carrierInfo.data,
    }),
  },
  beforeMount() {
    this.form.values = {
      name: this.carrierInfo.name,
      address: this.carrierInfo.address,
      state: this.carrierInfo.state,
      city: this.carrierInfo.city,
      zip: this.carrierInfo.zip,
      email: this.carrierInfo.email,
      contact_name: this.carrierInfo.contact_name,
      phone: this.carrierInfo.phone?.startsWith('+1') ? this.carrierInfo.phone.slice(2) : this.carrierInfo.phone,
      fax: this.carrierInfo.fax,
      terms_and_conditions: this.carrierInfo.terms_and_conditions,
    };
  },
  methods: {
    ...mapActions({
      editProfileInfo: constants.actions.EDIT_CARRIER_INFO,
    }),
    onSubmitProfileInfo() {
      const profileInfoFormData = this.form.values;

      this.editProfileInfo({ data: profileInfoFormData })
        .then(() => {
          this.$notificationSuccess('Your profile was successfully updated');
        }).catch(() => {
          this.$notificationError('Your profile wasn\'t updated');
        });
    },
  },
};
</script>

<style lang="scss" scoped>
  .change-password-link {
    @media #{map-get($display-breakpoints, 'lg-and-up')}{
      position: absolute;
      top: 20px;
      right: 20px;
    }
  }
</style>
